"use client"

import { Header } from "@/components/header"
import { Footer } from "@/components/footer"
import { WhatsAppButton } from "@/components/whatsapp-button"
import { useState, use } from "react"
import { Heart, Share2, Check } from "lucide-react"
import Link from "next/link"
import { mockProducts, mockColors } from "@/lib/mock-data"
import { notFound } from "next/navigation"
import Image from "next/image"

export default function ProductDetailPage({ params }: { params: Promise<{ id: string }> }) {
  const [quantity, setQuantity] = useState(1)
  const [selectedImage, setSelectedImage] = useState(0)

  const { id } = use(params)
  const product = mockProducts.find((p) => p.id === id)

  if (!product) {
    notFound()
  }

  const productImages = product.imagens.length > 0 ? product.imagens : ["/placeholder.svg"]
  
  // Get product colors from mockColors
  const productColors = mockColors.filter((c) => product.cores?.includes(c.id))

  return (
    <div className="min-h-screen flex flex-col">
      <Header />

      <div className="py-4 px-4 border-b border-border">
        <div className="max-w-7xl mx-auto text-sm text-foreground/60">
          <Link href="/" className="hover:text-foreground">
            Home
          </Link>{" "}
          /
          <Link href="/products" className="hover:text-foreground ml-1">
            Produtos
          </Link>{" "}
          /<span className="ml-1 text-foreground">{product.nome}</span>
        </div>
      </div>

      <div className="flex-1 py-12 px-4">
        <div className="max-w-7xl mx-auto">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-12">
            {/* Galeria de Imagens */}
            <div className="space-y-4">
              <div className="aspect-square rounded-xl overflow-hidden bg-muted border border-border">
                <Image
                  src={product.imagens[0] || "/placeholder.svg"}
                  alt={product.nome}
                  width={600}
                  height={600}
                  className="w-full h-full object-cover"
                />
              </div>
              <div className="grid grid-cols-4 gap-4">
                {product.imagens.slice(1, 5).map((img, i) => (
                  <div key={i} className="aspect-square rounded-lg overflow-hidden bg-muted border border-border">
                    <Image src={img || "/placeholder.svg"} alt={`${product.nome} ${i + 2}`} width={150} height={150} className="w-full h-full object-cover" />
                  </div>
                ))}
              </div>
            </div>

            {/* Informações do Produto */}
            <div className="space-y-6">
              <div>
                <div className="flex items-center justify-between mb-2">
                  <div className="flex items-center space-x-2 ml-auto">
                    <button className="text-foreground hover:text-accent transition-colors">
                      <Heart size={20} />
                    </button>
                    <button className="text-foreground hover:text-accent transition-colors">
                      <Share2 size={20} />
                    </button>
                  </div>
                </div>
                <h1 className="text-4xl font-light text-foreground mb-3">{product.nome}</h1>
                <div className="flex items-center space-x-4">
                  <div className="flex items-center space-x-2">
                    {[...Array(5)].map((_, i) => (
                      <span key={i} className="text-accent">
                        ★
                      </span>
                    ))}
                  </div>
                  <span className="text-sm text-foreground/60">(156 avaliações)</span>
                </div>
              </div>

              <div className="border-t border-b border-border py-6 space-y-4">
                <div className="flex items-baseline gap-3">
                  <span className="text-4xl font-medium text-primary">R$ {product.preco.toFixed(2)}</span>
                  {product.precoPromocional && (
                    <>
                      <span className="text-lg text-foreground/50 line-through">
                        R$ {product.precoPromocional.toFixed(2)}
                      </span>
                      <span className="text-sm bg-accent text-accent-foreground px-2 py-1 rounded">-20%</span>
                    </>
                  )}
                </div>
                <p className="text-sm text-foreground/70">Frete grátis para compras acima de R$ 100</p>
              </div>

              {/* Descrição */}
              <div className="space-y-3">
                <h3 className="font-medium text-foreground">Sobre este produto</h3>
                <p className="text-sm text-foreground/70">{product.descricao}</p>
                <ul className="space-y-2 text-sm text-foreground/70">
                  <li className="flex items-start space-x-2">
                    <Check size={16} className="mt-0.5 flex-shrink-0 text-accent" />
                    <span>Material premium de alta qualidade</span>
                  </li>
                  {product.personalizavel && (
                    <li className="flex items-start space-x-2">
                      <Check size={16} className="mt-0.5 flex-shrink-0 text-accent" />
                      <span>Completamente customizável e personalizável</span>
                    </li>
                  )}
                  <li className="flex items-start space-x-2">
                    <Check size={16} className="mt-0.5 flex-shrink-0 text-accent" />
                    <span>Entrega em até 15 dias úteis</span>
                  </li>
                  <li className="flex items-start space-x-2">
                    <Check size={16} className="mt-0.5 flex-shrink-0 text-accent" />
                    <span>Garantia de satisfação de 100%</span>
                  </li>
                </ul>
              </div>

              {/* Opções de Personalização */}
              {product.personalizavel ? (
                <div className="space-y-4 p-4 bg-muted/50 rounded-lg">
                  <h3 className="font-medium text-foreground">Opcões de Personalização</h3>

                  {/* Cor */}
                  {productColors.length > 0 && (
                    <div>
                      <label className="block text-sm font-medium text-foreground mb-2">Cor</label>
                      <div className="flex items-center space-x-3">
                        {productColors.map((color) => (
                          <button
                            key={color.id}
                            className="w-8 h-8 rounded-full border-2 transition-colors border-border hover:border-accent"
                            style={{ backgroundColor: color.codigoHex }}
                            title={color.nome}
                          />
                        ))}
                      </div>
                    </div>
                  )}

                  {/* Tamanho */}
                  <div>
                    <label className="block text-sm font-medium text-foreground mb-2">Tamanho</label>
                    <select className="w-full px-3 py-2 border border-border rounded-lg bg-background text-foreground text-sm">
                      <option>P - 10cm x 15cm</option>
                      <option>M - 15cm x 20cm</option>
                      <option>G - 20cm x 25cm</option>
                      <option>GG - 25cm x 30cm</option>
                    </select>
                  </div>

                  {/* Texto/Personalização */}
                  <div>
                    <label className="block text-sm font-medium text-foreground mb-2">Personalização de Texto</label>
                    <input
                      type="text"
                      placeholder="Seu texto aqui"
                      className="w-full px-3 py-2 border border-border rounded-lg bg-background text-foreground text-sm placeholder-foreground/50"
                    />
                  </div>
                </div>
              ) : (
                <div className="p-4 bg-muted/30 rounded-lg border border-border">
                  <p className="text-sm text-foreground/70">
                    Este produto não possui opções de personalização disponíveis.
                  </p>
                </div>
              )}

              {/* Quantidade e Adicionar ao Carrinho */}
              <div className="flex gap-4">
                <div className="flex items-center border border-border rounded-lg">
                  <button
                    onClick={() => setQuantity(Math.max(1, quantity - 1))}
                    className="px-4 py-2 text-foreground hover:bg-muted transition-colors"
                  >
                    -
                  </button>
                  <span className="px-4 py-2 border-l border-r border-border">{quantity}</span>
                  <button
                    onClick={() => setQuantity(quantity + 1)}
                    className="px-4 py-2 text-foreground hover:bg-muted transition-colors"
                  >
                    +
                  </button>
                </div>
                <button className="flex-1 bg-primary text-primary-foreground px-6 py-2 rounded-lg font-medium hover:bg-primary/90 transition-colors">
                  Adicionar ao Carrinho
                </button>
              </div>

              {/* Botão Customizar */}
              {product.personalizavel && (
                <p className="text-center w-full border-2 border-primary text-primary px-6 py-3 rounded-lg font-medium hover:bg-primary/5 transition-colors">
                  Entrar em contato via WhatsApp para personalizar
                </p>
              )}

              {/* Informações Adicionais */}
              <div className="space-y-2 text-xs text-foreground/60">
                <p>Categoria: {product.categoria}</p>
                <p>Marca: Memoriza</p>
              </div>
            </div>
          </div>

          {/* Produtos Relacionados */}
          <section className="mt-20 py-12 border-t border-border">
            <h2 className="text-2xl font-light text-foreground mb-8">Produtos Relacionados</h2>
            <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
              {[1, 2, 3, 4].map((relatedId) => {
                const relatedProduct = mockProducts.find((p) => p.id === relatedId.toString())
                if (!relatedProduct) return null
                return (
                  <Link href={`/products/${relatedProduct.id}`} key={relatedProduct.id} className="group cursor-pointer">
                    <div className="bg-muted rounded-lg overflow-hidden mb-4 aspect-square">
                      <img
                        src={relatedProduct.imagens[0] || "/placeholder.svg"}
                        alt={relatedProduct.nome}
                        className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
                      />
                    </div>
                    <h3 className="font-medium text-foreground">{relatedProduct.nome}</h3>
                    <p className="text-lg font-medium text-primary mt-2">R$ {relatedProduct.preco.toFixed(2)}</p>
                  </Link>
                )
              })}
            </div>
          </section>
        </div>
      </div>

      <Footer />
    </div>
  )
}
